.error-popup {
  position: fixed;
  top: 10px;
  right: 10px;
  max-width: 50%;
  visibility: hidden;
  opacity: 0;
  background: #df5151;
  display: flex;
  justify-content: left;
  padding: 10px;
  gap: 10px;
  align-items: center;
  border-radius: 0.5rem;
  transition: all 0.8s;
}
